.header-section{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin:5rem 0 5rem 0;
  
  opacity: 0;
  transition: 1s ease;
}
.fade-in{
  opacity: 1;
}

.sub-header-1{
  font-size: 1.2rem;
  font-weight: 300;
  display: flex;
  align-items: center;
}
.sub-header-2{
  font-size: 1.7rem;
  margin: 2rem;
  letter-spacing: 0.1rem;
}

